import * as echarts from "echarts/core"
// 返回3D柱状图renderItem,默认偏移
export const customeBar3DRenderItem = (params, api) => {
  // 维度的值转换成坐标系坐标
  // api.value(0),获取到x轴的数据，y轴的同理
  // api.coord([]),转化成坐标轴的坐标
  const location = api.coord([api.value(0), api.value(1)])
  // return type:"group",表明是一组元素
  return {
    type: "group",
    children: [
      {
        type: "CubeLeft",
        shape: {
          api,
          xValue: api.value(0),
          yValue: api.value(1),
          x: location[0],
          y: location[1],
          xAxisPoint: api.coord([api.value(0), 0])
        },
        style: {
          fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#33BCEB"
            },
            {
              offset: 1,
              color: "#337CEB"
            }
          ])
        }
      },
      {
        type: "CubeRight",
        shape: {
          api,
          xValue: api.value(0),
          yValue: api.value(1),
          x: location[0],
          y: location[1],
          xAxisPoint: api.coord([api.value(0), 0])
        },
        style: {
          fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#28A2CE"
            },
            {
              offset: 1,
              color: "#1A57B7"
            }
          ])
        }
      },
      {
        type: "CubeTop",
        shape: {
          api,
          xValue: api.value(0),
          yValue: api.value(1),
          x: location[0],
          y: location[1],
          xAxisPoint: api.coord([api.value(0), 0])
        },
        style: {
          fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#43C4F1"
            },
            {
              offset: 1,
              color: "#28A2CE"
            }
          ])
        }
      }
    ]
  }
}

/**
 *  自定义偏移返回renderItem
 * @param {*} offsetX 创建立体图侧面的相对数据点x的偏移
 * @param {*} offsetY 创建立体图侧面的相对数据点y的偏移
 * @returns
 */
export function createBar3DRenderItem(offsetX, offsetY) {
  const shape = offsetX && offsetY ? { offsetX, offsetY } : {}
  return (params, api) => {
    // 维度的值转换成坐标系坐标
    // api.value(0),获取到x轴的数据，y轴的同理
    // api.coord([]),转化成坐标轴的坐标
    const location = api.coord([api.value(0), api.value(1)])
    // return type:"group",表明是一组元素
    return {
      type: "group",
      children: [
        {
          type: "CubeLeft",
          shape: {
            api,
            xValue: api.value(0),
            yValue: api.value(1),
            x: location[0],
            y: location[1],
            xAxisPoint: api.coord([api.value(0), 0]),
            ...shape
          },
          style: {
            fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "#33BCEB"
              },
              {
                offset: 1,
                color: "#337CEB"
              }
            ])
          }
        },
        {
          type: "CubeRight",
          shape: {
            api,
            xValue: api.value(0),
            yValue: api.value(1),
            x: location[0],
            y: location[1],
            xAxisPoint: api.coord([api.value(0), 0]),
            ...shape
          },
          style: {
            fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "#28A2CE"
              },
              {
                offset: 1,
                color: "#1A57B7"
              }
            ])
          }
        },
        {
          type: "CubeTop",
          shape: {
            api,
            xValue: api.value(0),
            yValue: api.value(1),
            x: location[0],
            y: location[1],
            xAxisPoint: api.coord([api.value(0), 0]),
            ...shape
          },
          style: {
            fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "#43C4F1"
              },
              {
                offset: 1,
                color: "#28A2CE"
              }
            ])
          }
        }
      ]
    }
  }
}
