<template>
  <div class="right-bottom-container">
    <dv-border-box-3>
      <div class="flex h-full flex-col">
        <div class="chart-container">
          <el-carousel style="height: 100%; width: 100%" indicator-position="none" :interval="5000">
            <el-carousel-item style="height: 100%" v-for="item in 4" :key="item">
              <right-bottom-chart
                :xAxisData="xAxisData"
                :seriesData0="seriesData0"
                :title="title"></right-bottom-chart>
            </el-carousel-item>
          </el-carousel>
        </div>
      </div>
    </dv-border-box-3>
    <dv-decoration-3 class="right-middle-decoration" />
  </div>
</template>

<script>
import * as echarts from "echarts/core"
import { GridComponent } from "echarts/components"
import { LineChart } from "echarts/charts"
import { UniversalTransition } from "echarts/features"
import { CanvasRenderer } from "echarts/renderers"
import RightBottomChart from "@/views/Home/components/RightBottomPanel/components/RightBottomChart"
import { updateSeriesData } from "@/utils"

echarts.use([GridComponent, LineChart, CanvasRenderer, UniversalTransition])

export default {
  name: "RightBottomContainer",
  components: { RightBottomChart },
  props: {},
  data() {
    return {
      xAxisData: Array.from({ length: 30 }).map((_, i) => i + 1),
      seriesData0: Array.from({ length: 30 }).map(() => {
        return Math.floor(Math.random() * 50)
      }),
      title: "xxx设备月流速平均图"
    }
  },
  created() {},
  mounted() {
    setInterval(() => {
      this.seriesData0 = updateSeriesData(this.seriesData0, 1)
    }, 2000)
  }
}
</script>

