<template>
  <div class="h-full w-full" ref="chart"></div>
</template>
<script>
import * as echarts from "echarts/core"
import { GridComponent } from "echarts/components"
import { LineChart } from "echarts/charts"
import { UniversalTransition } from "echarts/features"
import { CanvasRenderer } from "echarts/renderers"

import { createBar3DRenderItem } from "@/utils/echarts-shared-options"

echarts.use([GridComponent, LineChart, CanvasRenderer, UniversalTransition])
export default {
  name: "",
  components: {},
  data() {
    return {
      chart: null
    }
  },
  props: {
    xAxisData: {
      type: Array,
      default: () => []
    },
    seriesData0: {
      type: Array,
      default: () => []
    },
    seriesData1: {
      type: Array,
      default: () => []
    },
    title: {
      type: String,
      default: ""
    }
  },
  computed: {
    option() {
      const res = {
        tooltip: {
          trigger: "axis",
          axisPointer: { type: "cross" }
        },
        title: {
          text: this.title
        },
        legend: {},
        xAxis: {
          type: "category",
          name: "时间",
          nameLocation: "end",
          nameGap: 20,
          data: this.xAxisData
        },
        yAxis: [
          {
            type: "value",
            name: "水位(mm)",
            position: "left"
          }
        ],
        series: [
          {
            name: "水位",
            type: "custom",
            renderItem: createBar3DRenderItem(4, 2),
            data: this.seriesData0
          }
        ]
      }
      return res
    }
  },
  methods: {
    initChart() {
      const chartDom = this.$refs.chart
      // 自定义主题
      this.chart = echarts.init(chartDom, "data-view-theme")
      this.option && this.chart.setOption(this.option)
    },
    setChartData() {
      if (this.chart) {
        this.chart.setOption(this.option)
      }
    }
  },
  watch: {
    xAxisData: {
      handler: "setChartData",
      deep: true
    },
    seriesData0: {
      handler: "setChartData",
      deep: true
    },
    seriesData1: {
      handler: "setChartData",
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart()
    })
  }
}
</script>
<style scoped></style>
