<template>
  <div class="deviceList-carousel-container">
    <dv-border-box-3>
      <div class="flex flex-col h-full">
        <div class="chart-container">
          <el-carousel style="height: 100%; width: 100%" indicator-position="none" :interval="5000">
            <el-carousel-item style="height: 100%" v-for="item in 4" :key="item">
              <left-top-chart
                :xAxisData="xAxisData"
                :seriesData0="seriesData0"
                :seriesData1="seriesData1"
                :title="title"
                barSize="small"></left-top-chart>
            </el-carousel-item>
          </el-carousel>
        </div>
      </div>
    </dv-border-box-3>
  </div>
</template>

<script>
import * as echarts from "echarts/core"
import { GridComponent } from "echarts/components"
import { LineChart } from "echarts/charts"
import { UniversalTransition } from "echarts/features"
import { CanvasRenderer } from "echarts/renderers"
import LeftTopChart from "@/views/Home/components/LeftSide/charts/LeftTopChart"
import { updateSeriesData } from "@/utils/index"

echarts.use([GridComponent, LineChart, CanvasRenderer, UniversalTransition])

export default {
  name: "LeftBottomPanel",
  components: { LeftTopChart },
  props: {},
  data() {
    return {
      // 每一个图表的数据
      xAxisData: Array.from({ length: 30 }).map((_, i) => i + 1),
      seriesData0: Array.from({ length: 30 }).map(() => {
        return Math.floor(Math.random() * 200 + 100)
      }),
      seriesData1: Array.from({ length: 30 }).map(() => {
        return Math.floor(Math.random() * 20 + 50)
      }),
      title: "xxx设备日平均图"
    }
  },

  computed: {},
  methods: {},
  created() {},
  mounted() {
    setInterval(() => {
      // update y1 data
      this.seriesData0 = updateSeriesData(this.seriesData0, 0)

      // update y2 data
      this.seriesData1 = updateSeriesData(this.seriesData1, 1)
    }, 2000)
  }
}
</script>
<style lang="scss" scoped>
.deviceList-carousel-container {
  flex: 1;
  padding: 20px;
  .chart-container {
    height: 100%;
    padding: 30px;
    position: relative;
    width: 100%;
  }
}
