<template>
  <div>
    <div
      class="content-body"
      :style="styleObj"
      ref="content_body"
      @transitionend="handleTransitionEnd">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {
    listData: {
      type: Array,
      required: true,
      default: () => []
    },
    // 每个item所需的时间
    speed: {
      type: [String, Number],
      default: 1
    }
  },
  data() {
    return {
      styleObj: {}
    }
  },

  computed: {
    canScroll() {
      return this.listData.length >= 5
    }
  },
  methods: {
    handleTransitionEnd(e) {
      this.styleObj = {
        transition: `transform 0s linear`,
        transform: "translate3d(0, 0, 0)"
      }

      setTimeout(() => {
        this.styleObj = {
          transition: `transform ${this.time}s linear`,
          transform: `translate3d(0, -${this.transformY - 1}px, 0)`
        }
      }, 0)
    },
    initScroll() {
      const $content = this.$refs.content_body
      // 复制多一份children
      const fragment = document.createDocumentFragment()
      ;[].map.call($content.children, child => {
        const cloneChild = child.cloneNode(true)
        fragment.appendChild(cloneChild)
      })
      $content.appendChild(fragment)

      const copyFirstChild = $content.children[Math.ceil($content.children.length / 2)]
      this.transformY = copyFirstChild.offsetTop
      this.time = this.listData.length * 2 * this.speed
      this.styleObj = {
        transition: `transform ${this.time}s linear`,
        transform: `translate3d(0, -${this.transformY - 1}px, 0)`
      }
    }
  },
  created() {},
  mounted() {
    this.canScroll && this.initScroll()
  }
}
</script>
<style lang="scss" scoped>
.content-body {
  position: relative;
}
</style>
